
import React from 'react'
import Link from 'gatsby-link'
//import GoogleMaps from '../components/maps'
import Sidebar from '../components/Sidebar'

const Logo = () =>
(
    <div className="error-page error-page--404">
    <div className="error__text">
    	<br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
      
    </div>
  </div>
);

const NotFound = ({tag}) =>
(
    <main className="backpage-content">
    <div className="error-page error-page--404"/>
    	<article>
            <div >
            	<p className="heading-content">You just hit a route that doesn&#39;t exist... the sadness.</p>
            	
			</div>
        </article>
     </main>
);

const NotFoundPage = () => (
	<div className="container backpage-main-content">
		<div className="row">
			<NotFound/>
			<Logo/>
		</div>
	</div>
)
export default NotFoundPage




